html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-instagram {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-twitter {
  width: 1em;
  height: 1em;
  fill: initial;
}

@font-face {
  font-family: FiraSans-Bold;
  src: url("../fonts/FiraSans-Bold.woff2") format("woff2"), url("../fonts/FiraSans-Bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: FiraSans-Italic;
  src: url("../fonts/FiraSans-Italic.woff2") format("woff2"), url("../fonts/FiraSans-Italic.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: FiraSans-Regular;
  src: url("../fonts/FiraSans-Regular.woff2") format("woff2"), url("../fonts/FiraSans-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: FiraSans-SemiBold;
  src: url("../fonts/FiraSans-SemiBold.woff2") format("woff2"), url("../fonts/FiraSans-SemiBold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  background: #FFF;
  font-family: FiraSans-Regular, Arial, sans-serif;
  color: #000;
}

.out {
  overflow-x: hidden;
  width: 100%;
  position: relative;
}

.out.is-hidden {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

a {
  text-decoration: none;
  font-size: inherit;
  color: inherit;
}

h1 {
  font-family: FiraSans-SemiBold, Arial, sans-serif;
  font-size: 120px;
  line-height: 0.93;
  text-transform: uppercase;
}

.line-bg {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding-top: 100vh;
}

.line-container {
  position: relative;
}

.main {
  padding: 100px 0;
  z-index: 2;
  position: relative;
}

.main-item {
  display: flex;
  margin-bottom: 100px;
}

.main-item__col {
  width: 50%;
}

.main-item__text {
  font-family: FiraSans-Bold, Arial, sans-serif;
  font-size: 44px;
  max-width: 670px;
  margin-left: 50px;
}

.main-item__subtext {
  font-size: 18px;
  max-width: 515px;
  margin-left: 50px;
  opacity: 0.5;
}

.header {
  position: absolute;
  top: 0;
  width: 100%;
  padding: 40px 0;
  background-color: transparent;
}

.header__content {
  display: flex;
  justify-content: flex-end;
  padding: 0 34px;
  align-items: center;
}

.header__logo {
  margin-right: 73px;
  z-index: 2;
}

.header__burger {
  z-index: 2;
  position: relative;
}

.logo {
  display: block;
  height: 60px;
  width: 270px;
}

.logo img {
  height: 100%;
  width: 100%;
}

.burger {
  cursor: pointer;
}

.burger span {
  transition: 0.3s;
  height: 5px;
  width: 36px;
  background-color: #ffffff;
  display: block;
}

.burger span:not(:last-child) {
  margin-bottom: 6px;
}

.burger.is-active span:nth-child(1) {
  transform: translate(1px, 12px) rotate(45deg);
}

.burger.is-active span:nth-child(2) {
  transform: translateX(100%);
  opacity: 0;
}

.burger.is-active span:nth-child(3) {
  transform: translate(1px, -10px) rotate(-45deg);
}

.hero {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  min-height: 100vh;
  display: flex;
  width: 100%;
  align-items: flex-end;
  justify-content: flex-end;
  padding: 200px 140px 50px;
}

.hero__content {
  max-width: 620px;
}

.hero__title {
  color: #fff;
  margin-bottom: 22px;
}

.hero__btns {
  display: flex;
  align-items: center;
}

.hero__btns .btn {
  margin-right: 20px;
}

iframe {
  height: 50px;
  width: 300px;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: FiraSans-SemiBold, Arial, sans-serif;
  text-transform: uppercase;
  color: #ffffff;
  width: 206px;
  height: 54px;
  border-radius: 8px;
  background-color: #f53e4f;
}

.appstore-btn {
  display: block;
  background-image: url("../img/appstore.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 216px;
  height: 64px;
  border-radius: 8px;
  transition: 0.3s;
}

.appstore-btn:hover {
  background-image: url("../img/appstore-rose.png");
}

.appstore-btn--footer {
  background-image: url("../img/appstore-rose.png");
}

.appstore-btn--footer:hover {
  background-image: url("../img/appstore.png");
}

.footer {
  background-color: #231f20;
  padding: 85px 40px;
}

.footer__logo {
  display: inline-block;
  position: relative;
  padding-bottom: 61px;
  margin-bottom: 61px;
}

.footer__logo-text {
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translateX(100%);
  width: 240px;
  height: 83px;
}

.footer__logo-text img {
  height: 100%;
  width: 100%;
}

.footer__content {
  max-width: 740px;
  margin: 0 auto;
}

.footer__desc {
  display: flex;
  align-items: center;
  margin-bottom: 56px;
}

.footer__download {
  margin-right: 36px;
  color: #ff5363;
  font-size: 13px;
}

.footer__download .appstore-btn {
  margin-bottom: 13px;
}

.footer__text {
  color: #ffffff;
  font-size: 13px;
  line-height: 1.54;
}

.footer__text span {
  background-image: linear-gradient(to right, #ff5464 0%, #f03546 100%);
  display: inline-block;
  text-align: center;
  width: 73px;
  height: 18px;
  border-radius: 5px;
  background-color: #f53e4f;
}

.footer__nav {
  color: #ffffff;
  text-align: center;
  margin-bottom: 21px;
}

.footer__social {
  text-align: center;
}

.nav {
  font-size: 13px;
}

.nav__item {
  display: inline-block;
}

.nav__item:not(:last-child) {
  margin-right: 45px;
}

.social__item {
  display: inline-block;
}

.social__item:not(:last-child) {
  margin-right: 45px;
}

.social__link {
  display: inline-block;
}

.social__link .icon {
  fill: #fff;
}

.menu {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background-color: #f53e4f;
  overflow-y: scroll;
  transition: ease-in-out 0.5s;
  padding: 0px 40px;
  display: flex;
  align-items: center;
  transform: translateX(100%);
}

.menu.is-open {
  transform: translate(0);
}

.menu__content {
  max-width: 875px;
  color: #ffffff;
  text-align: center;
  padding: 100px 0;
  margin: auto;
}

.menu__title {
  font-size: 73px;
  text-transform: uppercase;
  font-family: FiraSans-SemiBold, Arial, sans-serif;
  margin-bottom: 79px;
}

.menu__download {
  font-size: 13px;
  margin-bottom: 59px;
}

.menu__download .appstore-btn {
  margin: 0 auto;
  margin-bottom: 14px;
}

.menu__nav {
  margin-bottom: 22px;
}

.banner {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 200px 50px;
}

.banner__content {
  max-width: 740px;
  margin: 0 auto;
}

.banner__text {
  margin-bottom: 70px;
  font-size: 36px;
  font-family: FiraSans-Bold, Arial, sans-serif;
}

.banner__text .italic {
  font-family: FiraSans-Italic, Arial, sans-serif;
}

.banner__image {
  width: 100%;
}

.banner__image img {
  width: 100%;
  height: auto;
}
