.menu {
	z-index: 1;
	@include coverdiv;
	min-height: 100vh;
	background-color: #f53e4f;
	overflow-y: scroll;
	transition: ease-in-out 0.5s;
	padding: 0px 40px;
	display: flex;
	align-items: center;
	transform: translateX(100%);
	&.is-open {
		transform: translate(0); }
	&__content {
		max-width: 875px;
		color: #ffffff;
		text-align: center;
		padding: 100px 0;
		margin: auto; }
	&__title {
		font-size: 73px;
		text-transform: uppercase;
		@include semi;
		margin-bottom: 79px; }
	&__download {
		font-size: 13px;
		margin-bottom: 59px;
		.appstore-btn {
			margin: 0 auto;
			margin-bottom: 14px; } }
	&__nav {
		margin-bottom: 22px; } }

